@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,700&subset=latin-ext');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:700');

@-webkit-keyframes holo-motion {
  0%   { background-position: 0% 95%; }
  50%  { background-position: 35% 19%; }
  100% { background-position: 0% 95%; }
}

@keyframes holo-motion {
  0%   { background-position: 0% 95%; }
  50%  { background-position: 35% 19%; }
  100% { background-position: 0% 95%; }
}

* {
	font-family: 'Raleway', sans-serif;
}

body {
  overflow-y: scroll;
}

body, html{
	padding:0;
	margin:0;
	background: #4b6a7d; /* // 0e3658 */
	/* background: -moz-linear-gradient(top, #fbfbfb 0%, #ededed 46%, #ededed 100%); */
	/* background: -webkit-linear-gradient(top, #fbfbfb 0%, #ededed 46%, #ededed 100%); */
	/* background: linear-gradient(to bottom, #fbfbfb 0%, #ededed 46%, #ededed 100%); */
	text-align:center;
}

input {
  background: #4c6880;
  border: 1px solid #152838;
  border-radius: 4px;
  color: #152838;
  margin: 2px;
}

.hidden {
  visibility: hidden;
}

.visible {
  visibility: visible;
}

/*******************************************************
              React-Table Custom styles
*******************************************************/

.MuiListItem-root-111 {
  display: 'inline';
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0 auto;
}

.ReactTable .rt-table {
  overflow: hidden;
}


/* The below is : to select/highlight an entire row on hover  */
/* .ReactTable.-highlight .rt-tbody .rt-tr:not(.-padRow):hover {
  background: rgba(0, 0, 0, 0.05)
} */

.ReactTable.-striped .rt-tr.-even {
  background-color: #c3cdd6; /* #9eb2c3, #5c7388 */
}

.ReactTable.-striped .rt-tr.-odd {
  background-color: #63779082; /* #9eb2c3, #5c7388 */
}

.ReactTable .rt-tr {
  border: 1px solid #d8dee3;
  border-radius: 4px;
  margin: 3px;
}

.ReactTable .-pagination .-pageInfo {
  margin: 5px -7px;
}

.ReactTable .-pagination .-btn {
  color: #d8dee3;
  display: inline;
  width:86%;
}

.ReactTable .rt-thead.-filters input,
.ReactTable .rt-thead.-filters select {
  background: #c3cdd6 !important; /*alternative: #567dbb , #4a4c84 */
  color: #e0e9f6 !important;
  border-bottom: 2px solid #13426a;
}

.ReactTable .-pagination input,
.ReactTable .-pagination select {
  color: #4b6a7d;
  background-color: #b9c3cb;
  border: 2px solid #d8dee3;
  border-radius: 4px;
}

/*******************************************************
              Calendar Custom styles
*******************************************************/
.DateInput {
  background: transparent;
  color: white;
}

.DateInput_input__small {
  background: #c3cdd6; /* alt color: #13426a */
  color: 799ab6;
}

.DateRangePickerInput_arrow {
  color: #799ab6;
}

.DateRangePickerInput__withBorder {
  border: 1px solid #799ab6;
  background: #c3cdd6;
}


/*******************************************************
   Transaction-Type MUI Dropdown Button Custom styles
*******************************************************/
/* for the dropdown paper color:  */
.MuiPaper-root-92 {
  border-color: #789bb4;
}

/* for the dropdown mui button border title  */
.MuiFormLabel-root-531 {
  color: #13426a;
}

/* for the dropdown mui button border  */
.MuiOutlinedInput-root-545 .MuiOutlinedInput-notchedOutline-552 {
  border-color: #13426a !important;
}

.MuiSelect-selectMenu-552 {
  background: #13426a;
}



